import WucTab from '@/components/wuc-tab/wuc-tab.vue';
import dnIcon from '../../components/dn-icon/dn-icon.vue'
import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			userDto:{},
			id:'',
			// journalId:1,
			friend:'',
			photo:'',
			journal:'',
			journalList:[],
			juEs:[],
			bannercom: {},
			TabCur: 0,
			tabList: [{ name: '日志' }, { name: '相册' },{ name: '音乐' }],
			pictureNum:[]
			}
		},
    components: {
		dnIcon,
		WucTab,
		uniPopup
	},
	methods:{
		getPictureNum(){
			var _self = this;
			uni.request({
				url:this.$baseUrl + '/photoalbum/all',
				method:'POST',
				data:{
					id:this.userDto.id,
				},
				success: (res) => {
					console.log(res.data.data)
					this.pictureNum = res.data.data
				}
			});
		},
		// getBannercom() {
		// 	let data = {
		// 		journalId:this.journalId
		// 	};
		// 	uni.request({
		// 		url:this.$baseUrl+'/journal/user/comment/1',
		// 		method:'POST',
		// 		success: (data) => {
		// 			uni.stopPullDownRefresh();
		// 			if (data.statusCode == 200) {
		// 				this.bannercom = data.data;
		// 			}
		// 		},
		// 		fail: (data, code) => {
		// 		    console.log('fail' + JSON.stringify(data));
		// 		}
		// 	})
		// },
		getBanner() {
			let data= {
				id:this.userDto.id
			};
		    uni.request({
		        url: this.$baseUrl+'/journal/user/journaldetail/'+this.juEs.id,
				method:'POST',
		        success: (data) => {
		            uni.stopPullDownRefresh();
		            if (data.statusCode == 200) {
		                this.banner = data.data;
		            }
		        },
		        fail: (data, code) => {
		            console.log('fail' + JSON.stringify(data));
		        }
		    })
		},
		getJuEs(){
			var _self = this;
			uni.request({
				url:this.$baseUrl + '/journal/user/data',
				method:'POST',
				data:{
					id:this.id,
				},
				success: (res) => {
					console.log(res.data.data)
					this.juEs = res.data.data
				}
			});
		},
		tabChange(index) {
		    this.TabCur = index;
		},
		swiperChange(e) {
		    let { current } = e.target;
		    this.TabCur = current;
		},
		open(){
		        // 需要在 popup 组件，指定 ref 为 popup
		         this.$refs.popup.open()
		},
		goDetail(item){
			let detail = {
				id:item.id,
				userId:item.userId,
				thumbnail:item.thumbnail,
				createTime:item.createTime,
				comments:item.comments,
				title:item.title,
				avatar:item.avatar,
				nickname:item.nickname,
				likes:item.likes,
				content:item.content
			}
		    uni.navigateTo({
				url: "../myj_detail/myj_detail?detailDate=" + encodeURIComponent(JSON.stringify(detail))
		    })
		},
		userSum: function(userDto) {
					uni.request({
						url: this.$baseUrl + '/user/sum',
						method: 'POST',
						header: {
							'content-type': 'application/json'
						},
						data: {
							id: this.id
						},
						success: res => {
							if (res.data.code === 1) {
								console.log(res.data.data)
								this.userDto = res.data.data;
								this.friend = res.data.data.friendSum;
								console.log(this.friend)
								this.photo = res.data.data.photoAlbumSum;
								this.journal = res.data.data.journalSum;
							}
						}
					});
				},
		getComments(item){
					let comments = {
						id:item.id,
						content:item.content,
						createTime:item.createTime,
						userId:item.userId,
						nickname:item.nickname,
						avatar:item.avatar
					}
					url: "../myj_detail/myj_detail?detailDate=" + encodeURIComponent(JSON.stringify(comments))
				},
				
	},
 onLoad: function (option){
		console.log(option.id);
		this.id = option.id;
		console.log(this.id)
		this.userSum()
		
	}
}
